:root {
  --darkbrown: #382314;
  --mediumbrown: #93867b;
  --cream: #f8e9dd;
  --verypaleorange: #fffaf5;
  --softred: #ec775f;
  --cyan: #76b5bc;
  --spacing: 16px;
}

body {
  box-sizing: border-box;
  background-color: var(--cream);
  color: var(--darkbrown);
  margin: 0;
  padding: 0;
  font-family: DM Sans, sans-serif;
  font-size: 18px;
  font-weight: 400;
}

.flex {
  display: flex;
}

.justify-content-between {
  justify-content: space-between;
}

.font-bold {
  font-weight: 700;
}

.font-brown {
  color: var(--mediumbrown);
}

.font-sm {
  font-size: 80%;
}

.font-lg {
  font-size: 130%;
}

.text-right {
  text-align: right;
}

main {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.card {
  min-width: 350px;
  margin-bottom: var(--spacing);
  background-color: #fff;
  border-radius: 10px;
  padding: 16px;
}

.card-total {
  font-size: 200%;
}

.card--softred {
  background-color: var(--softred);
  color: #fff;
}

.card-chart {
  padding: calc(var(--spacing) * 2.5) 0;
  border-bottom: 2px solid var(--cream);
  justify-content: space-between;
  display: flex;
}

.chart-bar {
  background-color: var(--softred);
  width: 12%;
  height: auto;
  cursor: pointer;
  border-radius: 3px;
  align-self: flex-end;
  transition: all .6s;
  position: relative;
}

.chart-bar--active {
  background-color: var(--cyan);
}

.chart-bar:hover {
  opacity: .7;
}

.chart-name {
  text-align: center;
  color: var(--mediumbrown);
  font-size: 80%;
  position: absolute;
  bottom: -24px;
  left: 50%;
  transform: translateX(-50%);
}

.chart-tooltip {
  color: #fff;
  text-align: center;
  width: 120%;
  opacity: 0;
  background: #000;
  border-radius: 4px;
  padding: 4px 2px;
  font-size: 65%;
  transition: all .6s;
  display: none;
  position: relative;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
}

.chart-tooltip--visable {
  display: block;
}

.chart-tooltip--open {
  opacity: 1;
  margin-top: -10px;
}

